<template>
    <div>
        <HeaderComp></HeaderComp>
        <div class="statistics-container">
        <div class="d-flex">
            <div>
                <h2>Jinsi Bo'yicha Taqqoslash</h2>
                <apexchart type="pie"  width="400"
                           height="400" :options="chartOptions" :series="chartSeries" />
            </div>
            <table class="table table-bordered">
                <thead class="thead-dark">
                <tr>
                    <th scope="col">O'quvchi Ismi</th>
                    <th scope="col">Viloyati</th>
                    <th scope="col">Test Nomi</th>
                    <th scope="col">Jinsi</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(item,index) in countGender.foydalanuvchilar" :key="index">
                    <td>{{item.full_name}}</td>
                    <td>{{item.region_name}}</td>
                    <td>{{item.title}}</td>
                    <td>
                        <span v-if="item.jinsi==1">Qiz bola</span>
                        <span v-if="item.jinsi==0">O'g'il bola</span>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
        </div>
    </div>
</template>

<script>
    import HeaderComp from "@/components/HeaderComp.vue";

    export default {
        components: {
            HeaderComp,
        },
        data() {
            return {
                // Chart data
                chartSeries: [60, 40], // O'g'il va Qiz foizlari
                chartOptions: {
                    chart: {
                        type: 'pie',
                    },
                    labels: ['O\'g\'il', 'Qiz'],
                    legend: {
                        position: 'bottom',
                    },
                    responsive: [
                        {
                            breakpoint: 480,
                            options: {
                                chart: {
                                    width: '100%',
                                },
                            },
                        },
                    ],
                },
                countGender:[]
            }

        },
        mounted() {
            this.$axios.get('https://api-kasb.maktab.uz/api/v1/apisss/countGender/')
            .then(res=>{
                this.countGender=res.data
                this.chartSeries = [this.countGender.erkaklar_soni, this.countGender.ayollar_soni];
            })
        }
    }
</script>